import {customElement} from '@here/hds-base';
import {html, LitElement} from 'lit';
import {property} from 'lit/decorators.js';
import {classMap} from 'lit/directives/class-map.js';
import {styles} from './hds-step-connector.css.js';
import {StepperOrientation} from './hds-stepper.js';

/**
 * @summary Element to be used internally by the `hds-stepper`
 */
@customElement('hds-step-connector')
export class StepConnector extends LitElement {
  static styles = styles;

  @property({type: Boolean, reflect: true}) disabled = false;
  @property({type: Boolean, reflect: true}) active = false;
  @property({type: Boolean, reflect: true}) completed = false;
  @property({type: Boolean, attribute: false, reflect: true}) bottomLabel = false;
  @property({type: String, attribute: false, reflect: true}) orientation: StepperOrientation =
    'horizontal';
  @property({type: Number, attribute: false, reflect: true}) index = 0;

  protected render() {
    const classes = {
      'hds-step--label-bottom': this.orientation === 'horizontal' && this.bottomLabel,
      [`hds-step--orientation-${this.orientation}`]: true,
    };
    return html` <div class="hds-step-connector-wrapper ${classMap(classes)}">
      <span class="hds-step-connector"></span>
    </div>`;
  }
}

declare global {
  interface HTMLElementTagNameMap {
    'hds-step-connector': StepConnector;
  }
}
